.popup {
    $this: &;
    display: none;
    overflow-y: scroll;
    z-index: 30;
    @include coverdiv;
    background-color: rgba(0,0,0,0.8);

    &.is-open {
        opacity: 1;
        visibility: visible; }

    &__gallery {
        width: percentage(972/1440);
        position: relative;

        @include sm {
            width: 100%;
            height: 370px; } }

    &__body {
        width: 100%;
        padding: 66px 20px;
        @include center(h);
        border-radius: 2px;
        background-color: $bg-color--dark;
        box-shadow: inset 0px 0px 0px 20px $color-important;
        transition: box-shadow 0.6s linear;

        @include sm {
            padding: 40px 15px;
            box-shadow: inset 0px 0px 0px 10px $color-important; } }

    &__container {
        display: flex;
        flex-direction: column;
        max-width: #{1210 + ($padding--md * 2)}px;
        margin: 0 auto;
        padding: 0 #{$padding--md}px;

        @include sm {
            padding: 0 20px; }

        @include xs {
            padding: 0 15px; } }

    &__close {
        position: relative;
        height: 68px;
        width: 68px;
        margin-left: 17px;
        background-color: transparent;
        color: $color-important;
        transition: color .3s;

        &::before,
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 0;
            width: 100%;
            height: 2px;
            background: $color-important;
            transform: rotate3d(0, 0, 1, 45deg);
            transform-origin: 50% 50%;
            transition: background-color .3s; }

        &::after {
            transform: rotate3d(0,0,1,-45deg); }

        &:hover {
            color: $white;

            &::before,
            &::after {
                background-color: $white; } }

        @include xs {
            align-self: flex-start;
            order: 1;
            margin-top: 18px;
            margin-left: 0; } }

    &__close-text {
            padding-left: 91px; }

    &__header {
        display: flex;
        align-items: baseline;
        margin-bottom: 37px;

        .logo {
            margin-right: 30px; }

        .nav {
            height: auto;
            position: relative;
            top: auto;
            left: auto;
            right: auto;
            background: transparent;
            transform: inherit;
            padding: 0;
            overflow-y: auto;
            z-index: 10; }

        @include sm {
            margin-bottom: 26px; } }

    &__title {
        margin-bottom: 27px;

        &.sect-title {

            @include md {
                font-size: 150px; }

            @include sm {
                font-size: 100px; } } }

    &__sub-title {
        margin-bottom: 27px;
        text-align: left;
        padding-left: 28px;

        @include sm {
            padding-left: 0; } }

    &__descr {
        display: flex;
        justify-content: space-between;
        margin-bottom: 61px;
        padding-left: 28px;

        @include sm {
            flex-wrap: wrap;
            margin-bottom: 20px;
            padding-left: 0; } }

    &__info {
        width: 49%;
        font-size: 14px;
        font-weight: 400;
        line-height: 20px;
        letter-spacing: 0.14px;

        p {
            margin-bottom: 17px; }

        @include sm {
            width: 100%; } }

    &__footer {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include xs {
            flex-direction: column; } }

    &__btn {
        display: flex;
        align-items: center;
        flex-direction: row-reverse;

        .btn-info__descr {
            line-height: 20px;
            letter-spacing: .14px;
            margin-right: 26px;
            text-align: right; }

        @include sm {
            flex-direction: column;
            align-items: flex-start;

            .btn {
                margin-bottom: 10px; } }

        @include xs {
            width: 100%;

            .btn-info__descr {
                width: 100%;
                text-align: center;
                margin-right: 0; } } } }




