.btn {
	position: relative;
	overflow: hidden;
	display: inline-block;
	height: 60px;
	padding: 0 52px;
	background-color: $color-important;
	@include bebas-font;
	font-size: 24px;
	line-height: 60px;
	letter-spacing: 0.24px;
	font-weight: 400;
	color: #fcfcfd;
	text-align: center;
	text-decoration: none;
	border-radius: 5px;
	user-select: none;
	touch-action: manipulation;
	white-space: nowrap;
	transition: transform 0.5s ease-in-out;
	z-index: 3;

	&::before {
		z-index: -1;
		content: '';
		position: absolute;
		top: 0;
		right: -50px;
		bottom: 0;
		left: 0;
		border-right: 50px solid transparent;
		border-bottom: 80px solid #fff;
		transform: translateX(-100%);
		transition: 0.5s ease-in-out; }

	&:hover,
	&:focus {
		color: $color-important;

		&::before {
			transform: translateX(0); } }

	@include xs {
		width: 100%;
		padding: 0; }

	&--dark {
		color: $white;
		background-color: $bg-color; } }
