.btn-close {
	@include bebas-font;
	font-size: 18px;
	font-weight: 400;
	line-height: 1;
	letter-spacing: 0.18px;
	text-transform: uppercase;
	user-select: none;
	touch-action: manipulation;
	white-space: nowrap; }

.hero {
	min-height: 100vh;
	padding: 10em 0 0;
	font-size: 1rem;
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	justify-content: center;
	text-align: center;

	.letter {
		display: inline-block;
 }		// min-width:

	@include r(350) {
		padding: 6.5em 0 0; }

	&__content {
		// display: flex
 }		// flex-direction: column

	&__title {
		margin-bottom: 8px;

		@include md {
			font-size: 100px; }

		@include sm {
			font-size: 70px; }

		@include xs {
			margin-bottom: 31px;
			font-size: 30px; }

		@include r(350) {
			margin-bottom: 17px; } }

	&__sub-title {
		margin-bottom: 94px;

		@include sm {
			margin-bottom: 41px; }

		@include r(350) {
			margin-bottom: 20px; } }

	&__btn {
		margin-bottom: 20px; } }

.logo {
	position: relative;
	top: 5px;
	display: block;
	max-width: 151px;
	max-height: 33px;
	flex-shrink: 0;

	@include sm {
		top: 7px; }

	&__img {
		max-width: 100%;
		height: auto;
		object-fit: contain; } }

.main-title {
	position: relative;
	display: block;
	padding-top: 40px;
	@include bebas-font;
	font-size: 24px;
	line-height: 30px;
	letter-spacing: 0.24px;
	text-decoration: none;
	text-transform: uppercase;
	text-align: center;
	color: $white;

	pre {
		font-family: inherit;
		font-size: inherit;
		line-height: inherit;
		letter-spacing: inherit;
		text-transform: inherit;
		color: inherit;
		white-space: pre-wrap; }

	&::before {
		content: '';
		position: absolute;
		top: 0;
		@include center(h);
		@include arr(10,10, $white, t); }

	@include md {
		font-size: 22px; }
	@include sm {
		white-space: normal;
		font-size: 20px;
		line-height: 1.5;
		letter-spacing: 0.2px; }

	&--custom {
		padding-top: 57px; }

	&--alignment {
		text-align: left;

		&::before {
			left: 0;
			transform: none; } }

	&--alignment-sm {
		text-align: left;

		&::before {
			left: 0;
			transform: none; }

		@include sm {
			text-align: center;
			&::before {
				left: 50%;
				transform: translateX(-50%); } } }

	&--mob-left {
		@include xs {
			text-align: left; }

		&::before {
			@include xs {
				left: 0;
				transform: none; } } }

	&--default {
		padding: 0;

		&::before {
			display: none; } } }

.about-sect {
	padding: 29px 0;

	@include sm {
		padding: 38px 0; }

	&__title {
		margin-bottom: 39px;

		@include sm {
			margin-bottom: 9px; } }

	&__list {
		margin-bottom: 42px;
		display: flex;
		margin-left: -30px;
		margin-right: -30px;
		margin-bottom: 45px;

		@include sm {
			flex-wrap: wrap; }

		@include xs {
			margin-bottom: 0; } }

	&__btn {
		margin-bottom: 20px;
		text-align: center;

		.btn-default {
			padding: 0; }

		@include sm {
			text-align: left; } }

	&__item {
		width: 33.33%;
		padding: 0 30px;

		@include sm {
			width: 100%;

			&:not(:first-child) {
				display: none; } } } }

.about {
	@include clear-font;
	font-size: 14px;
	line-height: 1.43;
	letter-spacing: 0.14px; }

.btn-default {
	display: inline-block;
	height: 60px;
	padding: 0 26px;
	background-color: transparent;
	@include bebas-font;
	letter-spacing: 0.18px;
	font-size: 18px;
	line-height: 3.6;
	font-weight: 400;
	color: $color-important;
	text-decoration: none;
	user-select: none;
	touch-action: manipulation;
	white-space: nowrap;
	z-index: 3;

	&:hover,
	&:focus {
		opacity: 0.8; }

	@include xs {
		padding: 7px 22px;
		font-size: 18px; } }


.advantages-sect {
	&__title {
		margin-bottom: 39px; }

	&__list {
		display: flex;
		margin-right: -20px;
		margin-left: -20px;
		margin-bottom: 40px;

		@include md {
			flex-wrap: wrap;
			// justify-content: center
			margin-bottom: 30px; } }

	&__item {
		width: 20%;

		@include md {
			width: 33.33%;
			flex-grow: 1; }

		@include sm {
			width: 50%; } }


	&__btn {
		text-align: center; } }

.advantage {
	padding: 15px 20px;

	&__title {
		display: block;
		margin-bottom: 22px;
		font-size: 16px;
		line-height: 1.25;
		font-weight: 500;
		letter-spacing: 0.16px;
		text-decoration: none; }

	&__descr {
		display: block;
		font-size: 14px;
		line-height: 1.43;
		letter-spacing: 0.14px; }

	&__media {
		width: 16px;
		height: 16px;
		margin-bottom: 16px;

		& > svg {
			width: 100%;
			height: 100%;
			display: block;
			fill: $color-important; } } }

.details-sect {
	&__title {
		margin-bottom: 45px; }

	&__list {
		display: flex;
		margin-left: -8px;
		margin-right: -8px;
		margin-bottom: 50px;

		@include md {
			flex-wrap: wrap;
			justify-content: center;
			margin-bottom: 30px; }

		@include sm {
			flex-direction: column; } }

	&__item {
		width: 20%;

		@include md {
			width: 33.33%; }

		@include sm {
			width: 100%; } } }

.details-ico {
	padding-bottom: 60px;

	&__item {
		padding: 0 8px;
		width: 20%;

		@include sm {
			width: 100%;

			&:nth-child(2) {
				order: -1; } } }

	&__title {
		min-height: 56px;
		margin-bottom: 7px;
		padding-bottom: 15px;
		display: block;
		font-size: 16px;
		font-weight: 500;
		line-height: 1.25;
		letter-spacing: 0.16px;
		text-decoration: none;
		border-bottom: 1px solid rgba(#fdfefd, 0.20);

		&--margin {
			margin-bottom: 26px; } }

	&__descr {
		font-size: 14px;
		line-height: 1.43;
		letter-spacing: 0.14px; } }

.person {
	position: relative;

	&__info {
		@include xs {
			position: absolute;
			top: 50%;
			transform: translate(0, -50%);
			right: 0;
			width: 60%; } }

	&__img-wrap {
		position: relative;
		overflow: hidden;
		width: 160px;
		height: 160px;
		margin-left: -11px;
		margin-bottom: 36px;
		border-radius: 50%;
		opacity: 0.6;

		&::before {
			content: '';
			@include coverdiv;
			background-color: rgba($color-important, 0.73);
			transition: height .5s ease-in-out; }

		&:hover {
			&::before {
				height: 0; } }

		@include sm {
			margin-bottom: 0; } }

	&__img {
		display: block;
		max-width: 100%;
		height: 100%;
		object-fit: cover;
		border-radius: 50%; }

	&__name {
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		letter-spacing: 0.14px; }

	&__position {
		margin-bottom: 20px;
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		letter-spacing: 0.14px; }

	&__descr {
		font-size: 12px;
		font-weight: 400;
		line-height: 20px;
		letter-spacing: 0.12px;

		@include sm {
			display: none; } } }

.team-sect {
	padding: 37px 0;

	&__title {
		margin-bottom: 42px; }

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -18px; }

	&__item {
		width: 25%;
		margin-bottom: 75px;
		padding: 0 18px;

		@include sm {
			width: 50%; }

		@include xs {
			display: flex;
			width: 100%;
			margin-bottom: 20px;

			&:nth-child(even) {
				flex-direction: row-reverse;

				.person__info {
					left: 20px; } } } } }


.partners-sect {
	padding: 54px 0 85px;

	&__title {
		padding-right: 164px;
		padding-bottom: 6px;
		position: relative;
		top: -6px;

		@include sm {
			padding-right: 0;
			margin-bottom: 20px; } }

	&__wrap {
		display: flex;
		align-items: baseline;

		@include sm {
			flex-direction: column;
			align-items: center; } }

	&__list {
		display: flex;
		align-items: center;
		justify-content: space-between;
		flex-grow: 1;

		@include sm {
			flex-direction: column;
			align-items: center; } } }

.partner {
	@include xs {
		&:not(:last-child) {
			margin-bottom: 25px; } } }

.footer {
	padding: 77px 0 30px;
	background-color: $color-important;

	&__btn {
		padding-top: 51px;

		@include sm {
			width: 100%;
			order: -2;
			padding-top: 0; } }

	&__title {
		width: 34.5%;
		padding-right: 200px; }

	&__content {
		display: flex;
		flex-direction: column;
		flex-grow: 1;
		width: 65.5%;
		margin-top: 46px;

		@include sm {
			width: 100%; } }

	&__body {
		display: flex;
		margin-bottom: 91px;

		@include sm {
			flex-direction: column; } }

	&__articles {
		display: flex;
		justify-content: space-between;
		flex-grow: 1;
		margin-bottom: 17px; }

	&__articles-item {
		width: 45%;

		@include sm {
			width: 100%;

			&:not(:first-child) {
				display: none; } } }

	&__pagination {
		display: flex; }

	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;

		@include sm {
			flex-wrap: wrap; }
 }		// 	flex-direction: column

	&__copyriting,
	&__menu {
		opacity: 0.4;
		color: $white;
		font-size: 14px;
		font-weight: 500;
		line-height: 54px;

		@include sm {
			line-height: 20px; } }

	&__menu {
		@include sm {
			width: 70%; } }

	&__copyriting {
		@include sm {
			order: 3;
			width: 100%; } } }

.menu-info {
	display: flex;

	@include sm {
		flex-direction: column;
		line-height: 20px; }

	&__link {
		display: block;
		padding: 5px 15px;
		color: currentColor;
		text-decoration: none;

		&:hover {
			opacity: 0.85; } } }

.pagination {
	$this: &;

	&__item {
		position: relative;
		color: #fff;

		&.is-active {
			color: #11052f;

			&::before {
				content: '';
				position: absolute;
				top: 0;
				@include center(h);
				width: 40px;
				height: 40px;
				border: 1px solid #11052f;
				border-radius: 50%; } } }

	&__link {
		display: block;
		padding: 15px;
		padding: 0 15px;
		line-height: 38px;
		color: currentColor;
		text-decoration: none;
		transition: color .25s ease-in-out;

		&:hover {
			color: #11052f; } } }


.articles-list {
	// padding: 0 15px

	&__item {
		display: block;
		margin-bottom: 20px; } }

.article {
	$this: &;
	display: block;
	color: $white;
	text-decoration: none;

	&:hover {
		#{$this}__title {
			text-decoration: underline; } }

	&__title {
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		letter-spacing: 0.14px; }

	&__info {
		display: block;
		font-size: 14px;
		color: #3d0425;
		letter-spacing: 0.14px; } }

.social-list {
	display: flex;

	&__item {
		&:not(:last-of-type) {
			margin-right: 12px; } }

	&__link {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 34px;
		height: 34px;
		opacity: 0.4;
		transition: opacity 0.3 ease-in-out;

		svg {
			width: 34px;
			height: 34px;
			fill: $white; }

		&:hover {
			opacity: 0.7; } } }

.btn-info {
	&__descr {
		display: block;
		opacity: 0.2;
		@include avenir-font;
		color: #fdfcfd;
		font-size: 14px;
		font-weight: 400;
		line-height: 50px;
		letter-spacing: 0.14px; } }

.table {
	position: relative;

	@include xs {
		&::before {
			content: '';
			position: absolute;
			top: 45px;
			left: 0;
			height: 2px;
			width: 100%;
			background-color: #fdfcfd;
			opacity: .1;
			border-radius: 3px; } }

	thead > tr {
		height: 75px; }

	tbody td {
		padding-bottom: 15px;
		font-size: 14px;
		line-height: 2.86; }

	@include sm {
		width: 600px;
		overflow-x: auto;
		margin: 0 auto;
		top: auto;
		padding-bottom: 10px; } }


.advantages-sect {
	padding-top: 67px;
	padding-bottom: 67px; }

.statistical-sect {
	padding-top: 35px;
	padding-bottom: 35px; }




