.road-map {

	&__img {
		position: absolute;
		bottom: 0;
		left: 0;

		&--bottom {
			z-index: -1; }

		&--top {
			z-index: -2; } }

	&__item {
		position: absolute;
		font-size: 12px;
		color: #fff;
		line-height: 1.34;
		transform: translateY(-10px);

		&:nth-child(1) {
			bottom: 91px;
			left: 149px; }

		&:nth-child(2) {
			bottom: 159px;
			left: 410px; }

		&:nth-child(3) {
			bottom: 209px;
			left: 600px; }

		&:nth-child(4) {
			bottom: 349px;
			left: 1046px; } } }

