.language {
	$this: &;

	&__list {
		display: flex; }

	&__item {
		position: relative;

		&.is-active {
			#{$this}__link {
				color: #ffffff; } } }

	&__link {
		display: block;
		padding: 9px;
		color: $color-important;
		font-size: 14px;
		font-weight: 500;
		text-transform: uppercase;
		text-decoration: none;
		transition: color .3s;

		&:hover {
			color: #ffffff;
 } } }			// opacity: 0.8
