.sect-title {
	display: block;
	@include bebas-font;
	font-size: 12.500em;
	line-height: 1;
	font-weight: 100;
	letter-spacing: 2px;
	text-transform: uppercase;
	text-decoration: none;

	@include xs {
		font-size: 140px; }

	&__word {
		display: inline-block;
		padding-right: 15px;
		white-space: nowrap;

		&--wrap {
			padding-right: 0; } }

	@include r(360) {
		line-height: 120px;
		letter-spacing: 0; }

	@include r(350) {
		font-size: 90px;
		line-height: 1; }

	&__letter-t {
		letter-spacing: 1.4px; }

	&__letter-r {
		letter-spacing: 1.4px; }

	&__letter-i {
		letter-spacing: 1.4px; } }
