.nav {
	$this: &;
	display: block;
	color: $color-important;

	@include sm {
		height: 100vh;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		background: #fff;
		// transform: translateY(calc(-100% - 20px))
		transform: translateY(-100%);
		padding: 25px 0 15px 0;
		overflow-y: auto;
		z-index: 10;

		&.is-mob {
			transition: transform .4s cubic-bezier(.895,.03,.685,.22); }

		&.is-open {
			transform: translateY(0%);
 } }			//transform: translateY(calc(0% - 20px)/)

	&__list {
		display: flex;
		align-items: center;

		@include sm {
			flex-direction: column; } }

	&__item {
		&:last-child {
			#{$this}__link {
				padding-right: 0;

				@include sm {
					padding-right: 10px; } } } }

	&__link {
		position: relative;
		display: block;
		padding: 9px 17px;
		color: currentColor;
		font-size: 14px;
		font-weight: 500;
		text-transform: uppercase;
		text-decoration: none;
		transition: color .3s;

		&:hover,
		&:focus {
			color: $white;

			svg {
				fill: $white; } }

		&:active {
			opacity: 0.9; }

		@include sm {
			padding: 10px 15px; } }

	&__icon {
		display: inline-flex;
		align-items: flex-start;
		justify-content: center;
		width: 11px;
		height: 11px;
		margin-left: 5px;
		vertical-align: baseline;
		color: $color-important;

		svg {
			display: block;
			width: 10px;
			height: 12px;
			fill: currentColor; }

		.icon-pdf {
			display: block;
			width: 19px;
			height: 11px; }

		&--pdf {
			background-color: $color-important;
			border-radius: 4px;
			color: #04020e;
			width: 27px;
			height: 11px;
			border-radius: 2px; } } }

