.header {
	position: absolute;
	width: 100%;
	padding: 66px 0 20px 0;
	font-size: 1rem;
	background-color: transparent;

	.nav {
		margin-left: auto; }

	@include sm {
		padding: 36px 0 20px 0; }

	@include r(350) {
		padding: 15px 0 17px; }

	&__content {
		display: flex;
		align-items: baseline; }

	&__logo {
		margin-right: 80px;

		@include sm {
			margin-right: 32px; } }

	&__burger {
		display: none;
		top: 0;
		right: 0;
		z-index: 25;
		justify-content: center;
		align-items: flex-start;
		box-sizing: content-box;
		width: 24px;
		height: 24px;
		padding-left: 15px;
		flex-direction: column;
		margin-left: auto;
		position: relative;
		top: -10px;

		@include sm {
			display: flex; } } }


