.tabs {
	&__nav {
		z-index: 1;
		margin-bottom: 38px;
		text-align: center;

		@include sm {
			display: flex; }

		@include xs {
			margin-bottom: 20px; } }

	@include sm {
		&__link:not(:last-child) {
			margin-bottom: 18px; } }

	&__content:not(.is-active) {
		display: none; }

	&__link {
		position: relative;
		display: block;
		font-family: inherit;
		font-size: inherit;
		line-height: inherit;
		letter-spacing: inherit;
		text-decoration: none;
		text-transform: uppercase;
		text-align: inherit;
		color: inherit;
		background-color: transparent;
		outline: none;
		box-shadow: none;
		white-space: normal;
		width: 100%;
		text-align: left;
		padding-bottom: 23px;
		margin-bottom: 10px;

		&::before {
			display: none;
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			height: 2px;
			width: 100%;
			background-color: #fdfcfd;
			opacity: .1;
			border-radius: 3px; }

		&::after {
			display: none;
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			height: 2px;
			width: 360px;
			background-color: $color-important;
			border-radius: 3px;

			@include sm {
				width: 100%; } }

		&.is-active {
			color: $color-important;

			&::before,
			&::after {
				display: block; }

			@include sm {
				&::before {
					display: none; } } }

		&:hover {
			opacity: 0.9; }

		@include sm {
			width: auto;
			padding: 5px; } }


	&__table {
		position: relative;
		right: 0;
		top: -145px;
		padding-left: 390px;

		@include sm {
			top: 0;
			width: 100%;
			float: none;
			overflow: scroll;
			padding-left: 0; } } }

