.about-chart {
	position: relative;
	padding-top: 26px;
	padding-bottom: 26px;

	@include sm {
		padding-top: 0;
		padding-bottom: 30px; }

	&__title {
		position: absolute;
		top: 88px;

		@include sm {
			top: 82px; }

		@include xs {
			top: 80px; } }

	&__wrap {
		height: 450px;
		position: relative;
		width: auto; }

	&__item {
		height: 390px;
		width: 1198px;
		position: relative; }

	&__container {
		@include xs {
			padding: 0 0px 0 30px; } } }

