.scrollbar-external {
	height: 400px;
	overflow: auto;
	width: 100%; }

.external-scroll_x,
.external-scroll_y {
	cursor: pointer;
	display: none;
	position: absolute; }

.external-scroll_x.scroll-scrollx_visible {
	display: block;
	height: 4px;
	left: 0;
	top: 410px;
	width: 100%;
	background-color: rgba(255, 255, 255, 0.14); }

.external-scroll_y.scroll-scrolly_visible {
	display: block;
	height: 200px;
	left: 610px;
	top: 0;
	width: 10px; }


.external-scroll_x div,
.external-scroll_y div {
	height: 100%;
	left: 0;
	top: 0;
	position: absolute;
	width: 100%; }


.external-scroll_x .scroll-bar,
.external-scroll_y .scroll-bar {
	background: #fefbfd;
	z-index: 12; }


.external-scroll_x .scroll-bar {
	width: 38px;
	border-radius: 10px; }

.external-scroll_y .scroll-bar {
	height: 100px; }
